.App {
  text-align: center;
  color: #333;
  background-color: #fff;
  font-family: 'Helvetica', 'Arial', sans-serif;
}

.App-header {
  min-height: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.header_name {
  font-weight: 300;
  font-size: 60px;
  margin-bottom: 30px;
}

.header_info {
  font-weight: 400;
  font-size: 16px;
  max-width: 420px;
  line-height: 1.5;
  opacity: .7;
}
  
.section {
  display: flex;
  position: relative;
  width: 100%;
  min-width: 960px;
  min-height: 700px;
  overflow: hidden;
}

.wrapper {
  display: flex;
  width: 100%;
  min-width: fit-content;
  max-width: 1400px;
  justify-content: space-between;
  align-items: center;
  margin: auto;
}

.text-container {
  display: flex;
  flex-direction: column;
  width: 435px;
  padding-left: 50px;
  text-align: left;
  box-sizing: border-box;
  align-items: flex-start;
  color: white;
}

.bgImage {
  position: absolute;
  height: 100%;
  width: 100%;
  left: 40%;
  background-repeat: no-repeat;
  background-position-y: center;
}

.bossa .text-container img {
  height: 80px;
}

.skype .text-container img {
  height: 70px;
}

.nokia .text-container img {
  height: 30px;
}

.section-title {
  font-weight: 400;
  font-size: 26px;
  margin: 30px 0 20px;
}
.section-dates {
  font-weight: 400;
  font-size: 26px;
  opacity: .6;
  margin-left: 10px;
}

.section-info {
  font-size: 16px;
  opacity: .6;
  line-height: 1.5;
}

.skype .section-info {
  opacity: .7;
}

.App-footer {
  min-height: 80px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
